import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

const apiURL3 = 'http://localhost:8080/api/cards/';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'image/png'
  })
};

@Injectable({
  providedIn: 'root'
})
export class CardService {

  constructor(private httpClient: HttpClient) { }

  getAllCards(): Observable<any> {
    return this.httpClient.get(apiURL3);
  }

  getCardImage(id: number): Observable<any> {
    return this.httpClient.get(apiURL3 + 'images/' + id/*, httpOptions*/);
  }

  getCardImage2(idString: string): Observable<any> {
    return this.httpClient.get(apiURL3 + idString);
  }
}
