import { Component, OnInit } from '@angular/core';
import { CardService } from 'src/app/service/card.service';


@Component({
  selector: 'app-card-list',
  templateUrl: './card-list.component.html',
  styleUrls: ['./card-list.component.css']
})
export class CardListComponent implements OnInit {

  imageUrl = 'http://localhost:8080/api/cards/';
  cards: Array<any>;
  cardLength: number;

  constructor(private cardService: CardService) { }

  ngOnInit() {
  }

  displayCards() {
    // this.cards = null;
    this.cardService.getAllCards().subscribe(
      data => {
        this.cards = data;
      },
      error => console.log(error)
    );
  }

  displayRandomCards() {
    // this.cards = null;
    this.cardService.getAllCards().subscribe(
      data => {
        this.cardLength = data.length;
        this.cards = data;
        for (let index = 0; index < this.cardLength; index++) {
          let random = this.getRandomArbitrary(index, this.cardLength - 1);
          this.cards[index] = this.cards[random];
        }
      },
      error => console.log(error)
    );
  }

  getRandomArbitrary(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

}
